import { useContext } from "react";
import classnames from "classnames";
import { GlobalContext } from "states/context";
//import { useQuery } from "utils";
import { Header, ErrorBoundary, Footer } from "components/common";
import { MainBlock } from "components/elements/main";
import defaultStyles from "styles/default.module.scss";

const Home = (props) => {
  const [{ hasCovid }] = useContext(GlobalContext);
  //const params = useQuery(props.location);
  return (
    <ErrorBoundary>
      <div
        className={classnames(defaultStyles.pageContainer, {
          [defaultStyles.negative]: !hasCovid,
          [defaultStyles.positive]: hasCovid,
        })}
      >
        <Header />
        <MainBlock />
        <Footer />
      </div>
    </ErrorBoundary>
  );
};

export default Home;
