import { useMemo, useContext } from "react";
import classnames from "classnames";
import { words } from "language";
import { GlobalContext } from "states/context";
import { globalTypes } from "states/reducers/types";
import styles from "styles/footer.module.scss";

export const Footer = () => {
  const [{ language }, dispatch] = useContext(GlobalContext);
  const renderLanguages = useMemo(() => {
    return ["pt", "en", "fr"].map((val) => {
      return (
        <li
          key={val}
          onClick={() =>
            dispatch({ type: globalTypes.SET_LANGUAGE, value: val })
          }
          className={classnames({ [styles.active]: language === val })}
        >
          {val}
        </li>
      );
    });
  }, [language, dispatch]);

  return (
    <footer className={styles.footer}>
      <div className={styles.leftBlock}>
        <i className={classnames("flaticon-telephone", styles.icon)}></i>
        <span className={styles.description}>
          {words.linhaVerde[language]}: 800 209 498
        </span>
      </div>
      <div className={styles.rightBlock}>
        <ul>{renderLanguages}</ul>
      </div>
    </footer>
  );
};
