import { useContext } from "react";
import { GlobalContext } from "states/context";
import classnames from "classnames";
import { words } from "language";
import styles from "styles/main.module.scss";

export const MainBlock = () => {
  const [{ language, hasCovid }] = useContext(GlobalContext);

  return (
    <>
      <div
        className={classnames(styles.personalDataBlock, {
          [styles.negative]: !hasCovid,
          [styles.positive]: hasCovid,
        })}
      >
        <div className={styles.container}>
          <h1>{words.certificadoCovid19[language]}</h1>
          <div>
            <ul>
              <li>
                <strong>{words.analise[language]}:</strong> TESTE- Pesquisa de
                Coronavirus (COVID-19) [PCR]
              </li>
              <li>
                <strong>{words.resultado[language]}:</strong>{" "}
                <span
                  className={classnames({
                    [styles.negative]: !hasCovid,
                    [styles.positive]: hasCovid,
                  })}
                >
                  {!hasCovid
                    ? words.negativo[language]
                    : words.positivo[language]}
                </span>
              </li>
              <li>
                <strong>{words.amostra[language]}:</strong> 19/05/2021 - 20:05
              </li>
              <li>
                <strong>{words.metodo[language]}:</strong> Polimerase Chain
                Reaction
              </li>
              <li>
                <strong>{words.resultado[language]}:</strong> 28/05/2021 - 18:13
              </li>
              <li>
                <strong>{words.identificadorSinaveLab[language]}:</strong> ---
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className={styles.blockInfo}>
        <div className={classnames(styles.container, styles.content)}>
          <ul>
            <li
              className={classnames(styles.header, {
                [styles.negative]: !hasCovid,
                [styles.positive]: hasCovid,
              })}
            >
              <i
                className={classnames(
                  { "flaticon-tick": !hasCovid, "flaticon-virus": hasCovid },
                  styles.icon
                )}
              ></i>
              <span className={styles.description}>
                {!hasCovid
                  ? words.negativo[language]
                  : words.positivo[language]}
              </span>
            </li>
            <li>
              <strong>{words.laboratorio[language]}:</strong> Centro de Medicina
              Laboratorial Germano de Sousa
            </li>
            <li>
              <strong>{words.nome[language]}:</strong> TESTE QR CODE
            </li>
            <li>
              <strong>{words.docIdentificacao[language]}:</strong> PT-234432-ZW
            </li>
            <li>
              <strong>{words.amostra[language]}:</strong> XKK71764
            </li>
          </ul>
        </div>
      </div>
    </>
  );
};
